// import { PullRefresh } from 'vant'
import { getDiscList } from '../../api/recommend.js'
export default {
  data() {
    return {
      list: [],
      isLoading: false
    }
  },

  components: {
    // [PullRefresh.name]: PullRefresh
  },

  methods: {
    onRefresh() {
      setTimeout(() => {
        this.$toast('刷新成功')
        this.isLoading = false
      }, 500)
    },
    toDiscDetail(id) {
      this.$router.push('/discDetail/' + id)
    }
  },

  computed: {},
  created() {
    getDiscList().then(res => {
      this.list = res.data
    }).catch((err) => {
      console.log('getDiscList:', err)
    })
  },
  mounted() {}
}



