import {debounce} from '../../common/js/utils.js'

export default {
  props: {
    placeholder: {
      type: String,
      default: '搜索歌曲、歌手'
    },
    txt: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      query: this.txt
    }
  },
  methods: {
    clear() {
      this.query = ''
      this.$emit('clearSearch')
    },
    setQuery(query) {
      this.query = query
    },
    blur() {
      this.$refs.query.blur()
    }
  },
  created() {
    this.$watch('query', debounce((newQuery) => {
      this.$emit('query', newQuery)
    }, 200))
  },
  watch: {
    txt(newVal) {
      this.query = newVal
    }
  }
}



