export default {
  props: {
    delay: {
      type: Number,
      default: 2000
    },
    icon: {
      type: String,
      default: 'icon-ok'
    },
    tipTxt: {
      type: String,
      default: '提示~~'
    }
  },
  data() {
    return {
      showFlag: false
    }
  },
  methods: {
    show() {
      this.showFlag = true
      clearTimeout(this.timer)
      this.timer = setTimeout(() => {
        this.hide()
      }, this.delay)
    },
    hide() {
      this.showFlag = false
    }
  }
}



