
import musicTitle from '../musicTitle/musicTitle'
import search from '../search/search'
import searchResult from '../searchResult/searchResult'
import songHistory from '../songHistory/songHistory'
import { searchSong } from '../../api/song.js'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import {getSongsFromLocalStorage, history, searchHistory} from '../../common/js/utils.js'
export default {
data() {
return {
songList: '',
songType: history,
his: history,
seaH: searchHistory
}
},

components: {
musicTitle,
Tab,
TabItem,
search,
searchResult,
songHistory
},

methods: {
searchMusic(music) {
if (music) {
searchSong(music).then((res) => {
if (res.code === 200) {
this.songList = res.data
}
})
}
},
clearSearch() {
this.songList = ''
},
beforeEnter() {
this.$refs.search.clear()
},
handleClick(type) {
this.songType = type
}
},

computed: {},
created() {
this.songList = getSongsFromLocalStorage(history)
},
mounted() {}
}



