import { Tab, Tabs, Row, Col } from 'vant'
import back from '../back/back'

export default {
  data() {
    return {
      rotateBackBtn: ''
    }
  },
  props: {
    title: {
      type: String,
      default: '歌曲名称'
    },
    rotate: {
      type: String,
      default: '0'
    },
    singer: {
      type: String,
      default: '歌唱家'
    },
    isShowPlayer: {
      type: Boolean,
      default: false
    },
    isShowAddSong: {
      type: Boolean,
      default: false
    }
  },
  components: {
    back,
    [Tab.name]: Tab,
    [Tabs.name]: Tabs,
    [Row.name]: Row,
    [Col.name]: Col
  },

  methods: {},

  computed: {},
  created() {
    this.rotateBackBtn = {
      transform: `rotate(${this.rotate}deg)`
    }
  },
  mounted() {}
}



