
import Confirm from 'vux/src/components/confirm/index.vue'

export default {
data() {
return {
show: this.showConfirm
}
},
props: {
showConfirm: {
type: Boolean,
default: false
},
confirmText: {
type: String,
default: '确定'
},
cancelText: {
type: String,
default: '取消'
},
txt: {
type: String,
default: '确认执行操作?'
},
hasCancelEvent: {
type: Boolean,
default: false
},
hasConfirmEvent: {
type: Boolean,
default: false
},
hasShowEvent: {
type: Boolean,
default: false
},
hasHideEvent: {
type: Boolean,
default: false
}
},
components: {
Confirm
},

methods: {
onCancel() {
if (this.hasCancelEvent) {
this.$emit('onCancel')
}
},
onConfirm() {
if (this.hasConfirmEvent) {
this.$emit('onConfirm')
}
},
onShow() {
if (this.hasShowEvent) {
this.$emit('onShow')
}
},
onHide() {
if (this.hasHideEvent) {
this.$emit('onHide')
}
}
},

computed: {},
created() {},
mounted() {},
watch: {
showConfirm() {
this.show = this.showConfirm
}
}
}



