import {mapMutations} from 'vuex'
export default {
  data() {
    return {}
  },
  props: {
    isShowPlayer: {
      type: Boolean,
      default: false
    },
    isShowAddSong: {
      type: Boolean,
      default: false
    }
  },
  components: {},

  methods: {
    ...mapMutations({
      showPlayMusic: 'SHOW_PLAY_MUSIC',
      showAddSong: 'SHOW_ADD_SONG'
    }),
    back() {
      if (this.isShowPlayer) {
        this.showPlayMusic(false)
      } else if (this.isShowAddSong) {
        this.showAddSong(false)
      } else {
        this.$router.back()
      }
    }
  },
  computed: {},
  created() {},
  mounted() {}
}



